<template>
    <div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon"></i>
                    <a href="#"></a>
                </li>
                <li class="active"><router-link to="/navs">微服务</router-link></li>
                <li class="active">所长信箱</li>
            </ul>
        </div>
        <div class="page-content" :style="mianStyle">
            <div class="el-row contBox clear">
                <div :class="[onlyOneClass, isShowView ? twoClass : '']">
                    <div class="cs-filter-box cs-searchdiv">
                        <!-- 筛选 -->
                       <el-button-group>
                        <el-button :plain="flag != 2" type="primary" @click="filterTag(2)">全部</el-button>
                            <el-button :plain="flag != 0" type="primary" @click="filterTag(0)">未读</el-button>
                            <el-button :plain="flag != 1" type="primary" @click="filterTag(1)">已读</el-button>
                        </el-button-group>
                        <div class="cs-searchBox ">
                            <input type="text" placeholder="来信人姓名" autocomplete="off" class="el-input__inner cs-search_input" @keyup.enter="onEnter"  v-model="keyword">
                            <img src="../../img/lookup.png" @click="onEnter" alt="搜索">
                        </div>
                    </div>
                    <div class="cs-list-table">
                        <!-- 列表 -->
                        <div class="col-sm-6 no-padding-left" id="bszn_left">

                                <el-table
                                    :data="tableData"
                                    border
                                highlight-current-row
                                @row-click="rowclick">
                                    <el-table-column
                                      type="index"
                                      label="序号"
                                      width="65">
                                    </el-table-column>
                                    <el-table-column
                                      prop="realName"
                                      label="来信人姓名">
                                    </el-table-column>
                                    <el-table-column
                                      prop="title"
                                  show-overflow-tooltip
                                      label="信件标题">
                                    </el-table-column>
                                    <el-table-column
                                            label="来信时间">
                                        <template scope="scope">
                                            <span style="margin-left: 10px">{{scope.row.time|moment}}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column
                                      prop="content"
                                      label="信件内容"
                                  show-overflow-tooltip
                                  class="cs-conter">
                                    </el-table-column>
                                    <el-table-column
                                      label="操作"
                                        width="150">
                                      <template scope="scope">
                                        <el-button @click="lookClick(scope.$index, scope.row)" type="text" size="small">查看</el-button>
                                        <el-button @click="replyClick(scope.$index, scope.row)"  :disabled="scope.row.status != 2" type="text" size="small">回复</el-button>
                                        <el-button @click="deleteClick()" type="text" size="small">删除</el-button>
                                      </template>
                                  </el-table-column>
                                  </el-table>
                                  <div class="cs-footer-page">
                                <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="currentPage"
                                    :page-sizes="[10, 20, 30, 50]"
                                    :page-size="page_size"
                                    layout="total, sizes, prev, pager, next, jumper"
                                    :total="records">
                                    </el-pagination>
                                </div>

                        </div>
                    </div>
                </div>
                <div id="openinfo"  class="el-col el-col-24 el-col-xs-24 el-col-md-12 cs-col-right-box" v-if="isShowView" >

                    <div class="cs-szxx-box-hd"><span class="el-dialog__title">所长信箱</span>
                        <div class="el-dialog__headerbtn" @click="quxiao()"><i class="el-dialog__close el-icon el-icon-close"></i></div>
                    </div>
                    <div class="cs-szxx-dailog__body">
                        <div class="cs-xin-from cs-szxx-title">
                          <div><b>来信人姓名：</b>{{selectRowData.realName}}</div>
                          <div><b>联系电话：</b>{{selectRowData.phone}}</div>
                          <div><b>来信人时间：</b>{{selectRowData.time|moment}}</div>
                        </div>
                        <div class="cs-xin-content cs-szxx-content">
                            <div class="cs-szxx-view-main">
                                <div><b>来信标题:</b> {{selectRowData.title}} </div>
                                <div><b>来信内容:</b> {{selectRowData.content}}</div>
                            </div>

                          <el-input
                            type="textarea" :maxlength="50"
                            :autosize="{ minRows: 7, maxRows: 15}"
                            placeholder="请输入回复内容"
                            :disabled="selectRowData.status==1"
                            v-model="textarea3">
                        </el-input>
                       </div>
                        <div class="goback cs-back">
                            <el-button type="primary" @click="isreply?'':backClick()" :disabled="isreply"  >回复</el-button>
                            <el-button type="primary" @click="deleteClick()" >删除</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            mapHeight:{
                type:Number,
                default:600
            },
            tableStyle:{

            },
            twoClass: 'el-col-md-12',
            onlyOneClass: 'el-col el-col-24 el-col-xs-24',
            isShowView:false,
            tableData: [],
            selectRowData:{},
  	        textarea3: '',
  	        otoken:'',
  	        numID:'',
  	        userID:'',
  	        flag:2,
  	        oisRead:2,
  	        mianStyle:{},
  	        tableData: [],
            currentPage:1,
            page_size:10,
            records:0,//数据总数
            keyword:"",
            isreply:false
        }
    },
    components: {
		
    },
   	methods: {
      resetPage : function () {
          this.mapHeight = document.documentElement.offsetHeight  - 110;
          this.mianStyle = {
              height:this.mapHeight +"px"
          }
//          this.tableStyle = {
//              height:this.mapHeight-160 +"px"
//          }
      },
      rowclick(row){     //点击某一行
        this.isShowView = true;
        this.numID=row.id;
        this.selectRowData = row;
        this.numID=row.id;
        this.textarea3=row.replyContent;
        if(row.replyContent){
          this.isreply=true;
        }else{
          this.isreply=false;
        }

        let that = this;
        let token = global.data.getToken();
        if(!token) {
            return false;
        }
        this.$http({
            method:'POST',
            url:global.config.weburl +"api/msg/read.do",
            params:{token:token,id:this.numID}
        }).then(
            function(res){
              console.log(res)
            }
        );
        this.getTableData();
      },
	    lookClick(index, row) {   //查看
	        this.selectRowData = row;
	        this.numID=row.id;
          this.textarea3=row.replyContent;
          if(row.replyContent){
            this.isreply=true;
          }else{
            this.isreply=false;
          }
	    },
      replyClick(index, row) {  //进入回复
          this.selectRowData = row;
			    this.numID=row.id;
      },
      deleteClick() {    //删除
          let that = this;
	        let token = global.data.getToken();
	        if(!token) {
	            return false;
	        }
          this.$confirm('此操作将删除该信件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then((ref) => {
            if (ref) {   //删除留言
              if(!this.isShowView){
                this.isShowView = false;
              }
                this.$http({
                    method:'POST',
                    url:global.config.weburl +"api/msg/delete.do",
                    params:{token:token,id:this.numID}
                }).then(
                    function(res){
                      if(res.data.code==0){
                        this.$message({
                            type: 'info',
                            message: '删除成功'
                        });
                        this.isShowView = false;
                         this.getTableData();
                        // this.selectRowData="";
                      } else {
                          global.tools.handleError(res.data);
                      }
                    }
                );
              }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
          });          
        });
      },
      
      quxiao(){
         this.isShowView = false;
       },

	    backClick(){   //留言回复
		    let userinfo=sessionStorage.getItem('ha_ao_userinfo');
		    let userID=(JSON.parse(userinfo).id);
		    let that = this;
		        let token = global.data.getToken();
		        if(!token) {
		            return false;
		        }
        this.$http({
              method:'POST',
              url:global.config.weburl +"api/msg/reply.do",
              params:{token:token,id:this.numID,replyUserId:userID,replyContent:this.textarea3}
          }).then(
              function(res){
                if(res.data.code==0){
                  this.$message('回复成功！');
                }else{
                    global.tools.handleError(res.data);
                }
              }
          );
        //this.textarea3=this.replyContent;
	      this.isShowView = false;
        //this.oisRead = 0;
        //this.getTableData();
        this.filterTag(1);
	   },
			
      handleSizeChange(val) {
          this.page_size= val;
          this.getTableData();
          //console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
          this.currentPage = val;
          this.getTableData();
          //console.log(`当前页: ${val}`);
      },

      filterTag(type) {   //未读0，已读1，全部2
          this.quxiao();
         if(this.flag!=type){
         	 this.keyword="";
         }
         this.flag = type;
         this.oisRead = type;
         this.getTableData();
      },

      onEnter(){  //筛选框/回车/点击搜索开始筛选    
         this.keyword=this.keyword;           
         this.getTableData();
      },
            
  		getTableData() {
      	//let userID=global.data.getUserInfo().id;
      	
      	let that = this;
        let token = global.data.getToken();
        if(!token) {
            return false;
        }
				let _params = {token:token,type:2,page:that.currentPage, pageSize:that.page_size};
				if(that.oisRead != 2) {
					_params.isRead = this.oisRead;
				}
				if(this.keyword){
					_params.keyword=this.keyword;
				}
		        this.$http({
		            method:'GET',
		            url:global.config.weburl +"api/msg/list.do",
		            params:_params
		        }).then(
		            function(res){
		              this.tableData=res.data.data;
		              if(res.data.code==0){
		                    var _data= res.data;
		                    that.tableData = _data.data;
		                    that.records = _data.pageInfo.records;
		                }else{
                          global.tools.handleError(res.data);
		                }
		            }
		        );
		     }
		},
    created: function(){
    	this.resetPage();

        window.onresize = function(){
            that.resetPage();

        }
        this.getTableData();
    }
}
</script>
<style>
.cs-content{width: 90%;min-height: 10px;}
.el-pagination{width: 500px;float: right;margin-right: 20%; margin-top: 10px;}
.cs-conter{overflow: hidden!important;white-space: nowrap!important;text-overflow: ellipsis!important;width:270px;}
.goback{width: 100%; text-align: center; margin-top: 10px; }
.cs-quxiao{float: right;position: relative;right: 20px; top: -10px;}
.el-pagination{width: 70%;}
.cs-naumda b{margin-left: 20px;}
.cs-naumda b:nth-child(1){margin-left:0px;}
.cs-szxx-box-hd{padding: 0 0 20px;}
.cs-szxx-dailog__body{font-size: 16px;}
.cs-xin-from{font-size: 16px;border-bottom: 1px solid #d6d6d6;}
.cs-szxx-title{padding-bottom: 10px;}
.cs-szxx-view-main{margin-bottom: 10px;}
.cs-szxx-content{margin-top: 10px;}
</style>



